body {
    background-color: rgb(250, 250, 250);
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.image {
    width: 370px;
    margin-top: 12px;
}

.note {
    font-size: 14px;
    margin-top: 16px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    margin-bottom: -10px;
}


.title {
    text-align: end;
    color: #007bff;
    margin: 20px 0px;
}

@media (max-width: 770px) {
    .container {
        margin: 0 10%;
    }

    .image {
        width: 100%;
    }

    .title {
        text-align: center;
    }
}